.home {
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    padding: 0 80px;
    margin-top: var(--height-header);
    .infor {
        width: 60%;
        font-size: 52px;
        font-weight: 800;
        .name {
            color: var(--primary);
        }
    }
    .auto-write-wrapper {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        .auto-write {
            display: block;
            max-width: 600px;
            margin: 0 auto;
        }
    }
    .avatar {
        width: 40%;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
            width: 76%;
            border-radius: 50%;
            box-shadow: 0 0 10px rgba($color: #000000, $alpha: 0.3); 
            height: auto; 
            padding: 20px;
        }
    }
    .socials {
        position: absolute;
        bottom: 0;
        left: 80;
        display: flex;
        align-items: center;
        svg {
            font-size: 48px;
            margin-right: 20px;
            transition: color 0.3s ease-in-out;
            cursor: pointer;
            &:hover {
                color: var(--primary);
            }
        }
        .phones {
            align-items: center;
            a {
                display: flex;
                align-items: center;
                font-size: 28px;
                transition: color 0.3s ease-in-out;
                font-weight: 600;
                &:hover {
                    color: var(--primary);
                }
            }
        }
    }
} 
// Responsive
// Mobile
@media only screen and (max-width: 767px) {
    .home {   
        padding: 0 20px;
        justify-content: unset;
        .infor { 
            width: 100%;
            font-size: 32px; 
        }  
        .auto-write-wrapper{ 
            margin-bottom: 12px;  
            white-space: nowrap; 
            overflow: hidden; 
            text-overflow: ellipsis; 
        }
        .avatar {   
            display: none;
        }
        .socials { 
            svg { 
                font-size: 36px;
            } 
            .phones { 
                a { 
                    font-size: 20px;
                }
            } 
        }  
    }
}
