@import 'normalize.css';
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap'); 

:root {
    --primary: #fe2c55;
    --height-header: 80px;

    --background-light: #fff;
    --text-light: #1c1c1c;

    --background-dark: #0c0c0c;
    --text-dark: #ececec;

    --background-dark-header: #1f1f1f;
    --text-dark-header: #ececec;
} 
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
html {
    font-size: 62.5%;
}
body {
    font-family: 'Poppins', sans-serif;
    font-size: 1.6rem;
    line-height: 1.5;
    text-rendering: optimizespeed;
}  
// light/dark modde 
body { 
    transition: color 0.3s ease-in-out;
}
body.dark {
    background-color: var(--background-dark);
    color: var(--text-dark);
}  
.dark {
    background-color: var(--background-dark) !important;
    color: var(--text-dark) !important;
}  
.dark-header {
    background-color: var(--background-dark-header) !important;
    color: var(--text-dark-header) !important;
} 
.dark-header-link { 
    color: unset !important; 
}
.header-mobile nav .dark-header {
    background-color: var(--background-dark-header);
    color: var(--text-dark-header);
} 
#header.dark-header {
    background-color: var(--background-dark-header);
    color: var(--text-dark-header);
} 
// remove default css
a {
    text-decoration: none;
    color: #1f1f1f;
} 
button { 
    background-color: unset;
} 
.active {
    border-bottom: 4px solid var(--primary);
}


